import "./hub.css"
import EndzoneNav from '../../../components/navigators/endzone-nav/EndzoneNav';
import SideNav from "../../../components/navigators/endzone-side-nav/side-nav";
import ToolFolder from "../../../components/tools/endzone-tool-folder/tool-folder";
import Footer from "../../../components/misc-items/footer/footer";
import PostAddIcon from '@mui/icons-material/PostAdd';
import SportsFootballIcon from '@mui/icons-material/SportsFootball';
import UnfoldMoreDoubleIcon from '@mui/icons-material/UnfoldMoreDouble';
import SchoolIcon from '@mui/icons-material/School';
import MenuBookIcon from '@mui/icons-material/MenuBook';
import HeadsetMicIcon from '@mui/icons-material/HeadsetMic';
import SummarizeIcon from '@mui/icons-material/Summarize';
import AssessmentIcon from '@mui/icons-material/Assessment';

function Hub()
{
    var search_data = [
        { name: "Home", tags: ["Home", "Main Menu", "Endzone"], link: "/" },
        { name: "Create Data", tags: ["Create", "Data"], link: "/endzone/data/game" },
        { name: "Create Game", tags: ["Create", "Game", "Data", "New"], link: "/endzone/data/game" },
        { name: "Data Viewer", tags: ["Data", "View", "Explore"], link: "/endzone/data/viewer" },
        { name: "Explore Dashboard", tags: ["Pregame", "Viewer", "Dashboard", "Explore"], link: "/endzone/pregame/dashboard" },
        { name: "Game Simulator", tags: ["Pregame", "Game", "Simulator"], link: "/endzone/pregame/simulator" },
        { name: "Manage Data", tags: ["Data", "Manage", "Management"], link: "/endzone/data/manage" },
        { name: "Manage Formations", tags: ["Coaches", "Corner", "Formation", "Playbook"], link: "/endzone/coaches-corner/formation" },
        { name: "Manage Game", tags: ["Data", "Manage", "Management"], link: "/endzone/data/manage" },
        { name: "Model Builder", tags: ["Pregame", "Models", "AI", "Predict"], link: "/endzone/pregame/models" },
        { name: "Pregame Report", tags: ["Pregame", "Tool", "Report", "Generator"], link: "/endzone/pregame/report" },
        { name: "Thunderbolt Report", tags: ["Pregame", "Report", "Thunderbolt"], link: "/endzone/pregame/thunderbolt" },
        { name: "Upload Data", tags: ["Data", "Upload", "HUDL", "QkikCut", "new"], link: "/endzone/data/upload" },
        { name: "Upload Film", tags: ["Film", "Upload", "New", "Video", "Hudl", "QkikCut"], link: "/endzone/data/film/upload" },
        { name: "View Data", tags: ["View", "Data"], link: "/endzone/data/viewer" },
        { name: "Watch Film", tags: ["Data", "Film", "Watch", "Explore", "View"], link: "/endzone/data/film/watch" }      
    ];

    return (
        <div className="hubPage">
            <EndzoneNav links={[{title: "Logout of Endzone", link: "/logout"},]}></EndzoneNav>
            <div className="hub-body">
                <div className="side-nav">
                    <SideNav search_data = {search_data} links = {search_data}></SideNav>
                </div>
                <div className="hub-menu">
                    <div className="hub-left">
                        <ToolFolder folder_name = "Data Management"
                            tools = {
                                [
                                    {"tool_name": "New Game", "tool_text": "Create new data with manual or Ai-Assisted entry", "tool_link": "/endzone/data/game", "icon": (<PostAddIcon sx = {{"font-size": '50px'}} className='tool-icon' ></PostAddIcon>)},
                                    {"tool_name": "View More", "tool_text": "Click to see what the data hub has to offer.", "tool_link": "/endzone/data/hub", "icon": (<UnfoldMoreDoubleIcon sx = {{"font-size": '50px'}} className='tool-icon' ></UnfoldMoreDoubleIcon>)}
                                ]
                            }>
                        </ToolFolder>
                        
                        <ToolFolder folder_name = "Coaches Corner"
                            tools = {
                                [
                                    {"tool_name": "Formations", "tool_text": "Manage your team's formations", "tool_link": "/endzone/coaches-corner/formation", "icon": (<SportsFootballIcon sx = {{"font-size": '50px'}} className='tool-icon' ></SportsFootballIcon>)},
                                    {"tool_name": "View More", "tool_text": "Click to see what the coaches corner has to offer.", "tool_link": "/endzone/coaches-corner/hub", "icon": (<UnfoldMoreDoubleIcon sx = {{"font-size": '50px'}} className='tool-icon' ></UnfoldMoreDoubleIcon>)}
                                ]
                            }>
                        </ToolFolder>

                        <ToolFolder folder_name = "Endzone Clinic"
                            tools = {
                                        [
                                            {"tool_name": "Endzone-U", "tool_text": "Watch tutorials on how to use our platform", "tool_link": "/", "icon": (<SchoolIcon sx = {{"font-size": '50px'}} className='tool-icon' ></SchoolIcon>)},
                                            {"tool_name": "Docs", "tool_text": "Read through walk-through's, and examples of Endzone's tools", "tool_link": "/", "icon": (<MenuBookIcon sx = {{"font-size": '50px'}} className='tool-icon' ></MenuBookIcon>)}
                                        ]
                                    }>
                        </ToolFolder>
                    </div>
                    <div className="hub-right">
                        <ToolFolder folder_name = "Pre-Game Tools"
                            tools = {
                                        [
                                            {"tool_name": "Scouting", "tool_text": "Create a pre-game report for an upcoming opponent.", "tool_link": "/endzone/pregame/report", "icon": (<SummarizeIcon sx = {{"font-size": '50px'}} className='tool-icon' ></SummarizeIcon>)},
                                            {"tool_name": "View More", "tool_text": "Click to view all pre-game tools", "tool_link": "/endzone/pregame/hub", "icon": (<UnfoldMoreDoubleIcon sx = {{"font-size": '50px'}} className='tool-icon' ></UnfoldMoreDoubleIcon>)}
                                        ]
                                    }>
                        </ToolFolder>
                        <ToolFolder folder_name = "In-Game Tools"
                            tools = {
                                    [
                                        {"tool_name": "Coach Mike", "tool_text": "Predict plays in real time with Coach Mike", "tool_link": "/", "icon": (<HeadsetMicIcon sx = {{"font-size": '50px'}} className='tool-icon' ></HeadsetMicIcon>)},
                                        {"tool_name": "View More", "tool_text": "Click to view all in-game tools", "tool_link": "/", "icon": (<UnfoldMoreDoubleIcon sx = {{"font-size": '50px'}} className='tool-icon' ></UnfoldMoreDoubleIcon>)}
                                    ]
                                }>
                        </ToolFolder>

                        <ToolFolder folder_name = "Post-Game Tools"
                            tools = {
                                [
                                    {"tool_name": "Recap Report", "tool_text": "Just got off the field? Let's recap what happened.", "tool_link": "/", "icon": (<AssessmentIcon sx = {{"font-size": '50px'}} className='tool-icon' ></AssessmentIcon>)},
                                    {"tool_name": "View More", "tool_text": "Click to view all post-game tools", "tool_link": "/", "icon": (<UnfoldMoreDoubleIcon sx = {{"font-size": '50px'}} className='tool-icon' ></UnfoldMoreDoubleIcon>)}
                                ]
                            }>
                        </ToolFolder>
                    </div>
                </div>
            </div>
            <Footer></Footer>
        </div>
    );
}

export default Hub;