import React from 'react';
import "./tool-page.css"
import EndzoneNav from '../../../components/navigators/endzone-nav/EndzoneNav';
import EndzoneButton from '../../../components/buttons/endzone-new-button/endzone-button' 
import EndzoneGameSelect from '../../../components/selectors/endzone-game-select/endzone-game-select';
import PlayCircleOutlineIcon from '@mui/icons-material/PlayCircleOutline';
import SettingsIcon from '@mui/icons-material/Settings';
import logo from './images/Endzone_Shield.png'
import axios from 'axios'
import Checkbox from '@mui/material/Checkbox';
import FormControlLabel from '@mui/material/FormControlLabel';
import { Typography , createTheme } from '@mui/material';
import Box from '@mui/material/Box';
import EndzoneAlert from '../../misc-items/endzone-alert/endzone-alert';

const theme = createTheme({
    typography: {
      fontFamily: 'Audiowide',
    }
})

class ToolPage extends React.Component {
    constructor (props) {
        super(props);
        this.state = {
            isLoading: false,
            isBaseScreenActivated: true,
            isConfigureActivated: false,
            viewUrl: false,
            documentUrl: '',
            pack_data: null,
            game_data: [
                {
                  Away_Team: "Eagan",
                  Creation_Date: "Sun, 14 May 2023 15:06:14 GMT",
                  Game_Date: "Tue, 02 May 2023 00:00:00 GMT",
                  Game_ID: "1df6d4aa-fef9-490d-ac53-9adf0763c27f",
                  Game_Type: "manual",
                  Home_Team: "Eastview",
                  id: 0,
                },
                {
                  Away_Team: "Test 2",
                  Creation_Date: "Wed, 17 May 2023 02:22:05 GMT",
                  Game_Date: "Tue, 02 May 2023 00:00:00 GMT",
                  Game_ID: "fa3b0533-a178-4b9f-a3ac-a63965a7e8f1",
                  Game_Type: "manual",
                  Home_Team: "Test 1",
                  id: 1,
                },
                {
                  Away_Team: "Test 2",
                  Creation_Date: "Wed, 17 May 2023 23:34:48 GMT",
                  Game_Date: "Mon, 01 May 2023 00:00:00 GMT",
                  Game_ID: "1a32cdde-f341-45c8-a217-352fb9c7d19b",
                  Game_Type: "manual",
                  Home_Team: "Test",
                  id: 2,
                },
                {
                  Away_Team: "Pogville",
                  Creation_Date: "Sat, 20 May 2023 04:27:44 GMT",
                  Game_Date: "Wed, 03 May 2023 00:00:00 GMT",
                  Game_ID: "2e78ebf0-6761-4ae9-9cb8-a4dd124d5551",
                  Game_Type: "manual",
                  Home_Team: "Eastview",
                  id: 3,
                },
                {
                  Away_Team: "Tongo Tech",
                  Creation_Date: "Sun, 28 May 2023 16:57:55 GMT",
                  Game_Date: "Mon, 22 May 2023 00:00:00 GMT",
                  Game_ID: "89f2b898-d09a-4e78-9287-896dbf00d5da",
                  Game_Type: "manual",
                  Home_Team: "Eastview",
                  id: 4,
                },
                {
                  Away_Team: "test white",
                  Creation_Date: "Sun, 28 May 2023 17:10:37 GMT",
                  Game_Date: "Wed, 03 May 2023 00:00:00 GMT",
                  Game_ID: "18ddc41c-475b-45ae-9561-f2ea7f1181e5",
                  Game_Type: "manual",
                  Home_Team: "test",
                  id: 5,
                },
                {
                  Away_Team: "PogsWorth",
                  Creation_Date: "Mon, 29 May 2023 19:04:06 GMT",
                  Game_Date: "Tue, 02 May 2023 00:00:00 GMT",
                  Game_ID: "04b683a7-a84c-401b-9159-dc6b072d83e1",
                  Game_Type: "manual",
                  Home_Team: "Eastview",
                  id: 6,
                }
            ],
            game_objects: [],
            year_options: [],
            team_options: [],
            game_options: [],
            selected_games: [],
            selected_years: [],
            selected_teams: []
            
        }
        this.selected_options = []
        this.alertRef = React.createRef()
    }

    componentDidMount = () => {
        this.getToolMetaData()
        this.getGames()
        
    }

    getToolMetaData = () => {
        axios.get('/endzone/reports/pregame/metadata').then((response) => {
          this.setState({pack_data: response.data})
        }, (error) => {
          //To-Do display error
          this.setState({pack_data: null})
        })

    }
    
    getGames = async () => {
        axios.get('/endzone/data/game/get').then((response) => {
            this.setState({game_data: response.data}, () => {
                this.loadGames(this.state.game_data)
            })
          }, (error) => {
            //To-Do display error
            this.setState({game_data: null})
          })
    }

    loadGames = (game_data) => {
        let teams_of_interest_setList = new Set()
        let years_setList = new Set()
        let game_object = []

        game_data.map((game) => {
            if(!teams_of_interest_setList.has(game.Away_Team)) {   
                teams_of_interest_setList.add(game.Away_Team)
            }
            if(!teams_of_interest_setList.has(game.Home_Team)) {
                teams_of_interest_setList.add(game.Home_Team)
            }
            let game_date = new Date(game.Game_Date)
            let year = game_date.getUTCFullYear()
            if(!years_setList.has(year)){
                years_setList.add(year)
            }

            let game_string = game.Home_Team + " vs " + game.Away_Team + " - " + game_date.toDateString()
            console.log(game)
            game_object.push({gameLabel: game_string, Home_Team: game.Home_Team, Away_Team: game.Away_Team, Year: game_date.getUTCFullYear(), Game_ID: game.Game_ID})

        }) 
        this.setState({
            year_options: this.convertSingularListToSelectDict([...years_setList]),
            team_options: this.convertSingularListToSelectDict([...teams_of_interest_setList]),
            game_objects: game_object
        }, () => {
            this.filterGames()
        })
    }

    filterGames = () => {
        let game_objects = this.state.game_objects;
        let filtered_objects = [];
        let selected_years = this.state.selected_years;
        let selected_teams = this.state.selected_teams;

        
        game_objects.map((game_object) => {
            console.log(game_object.Away_Team)
            if((selected_teams.length == 0  || selected_teams.includes(game_object.Away_Team) || selected_teams.includes(game_object.Home_Team)) && (selected_years.length == 0  || selected_years.includes(game_object.Year))) {
                filtered_objects.push({value: game_object.Game_ID, label: game_object.gameLabel})
            }
            
        })
        this.setState({game_options: filtered_objects})
    }

    convertSingularListToSelectDict = (list) => {
        let updated_list = []
        list.map((elm) => {
            updated_list.push({value: elm, label: elm})
        }) 
        return updated_list
    }

    runTool = async () => {
        if(this.state.selected_teams.length == 0) {
            this.alertRef.current.openAlert("A team of Interest is required to generate a report.", "warning");
            return;
        }
        else if(this.state.selected_games.length == 0) {
            this.alertRef.current.openAlert("At least one game is required to create a report.", "warning");
            return;
        }
        this.alertRef.current.closeAlert();

        this.runtimeLoading();
        await this.requestReport();
        this.runtimeComplete();
    }

    activateConfigure = () => {
        this.setState({
            isConfigureActivated: !this.state.isConfigureActivated,
            isBaseScreenActivated: !this.state.isBaseScreenActivated,
            isLoading: false
        })
    }

    requestReport = async () => {
        await axios.get(
            this.props.requestUrl, {
                params: {
                    requestedPages: this.selected_options.toString(),
                    requestedTeamOfInterest: this.state.selected_teams.toString(),
                    requestedGames: this.state.selected_games.toString()
                },
                responseType: 'blob'
            }
        ).then (
            (response) => {
                const blobUrl = URL.createObjectURL(new Blob([response.data], { type: 'application/pdf' }));
                this.setState({documentUrl: blobUrl, viewUrl: true})
                window.open(blobUrl, '_blank');
            },
            (error) => {
                this.alertRef.current.handleRequestError(error)
            }
    )}

    runtimeLoading =  () => {
        this.setState({
            isBaseScreenActivated: true,
            isLoading: true,
            isConfigureActivated: false,
        })
    }

    runtimeComplete = () => {
        this.setState({
            isBaseScreenActivated: true,
            isLoading: false,
            isConfigureActivated: false
        })
    }

    handleConfigChange = (e, id) => {
      console.log(e.target.checked)
      e.target.checked ? this.addPack(id) : this.removePack(id);
      console.log(this.selected_options)
    }

    addPack = (id) => {if(!this.selected_options.includes(id)){this.selected_options.push(id)}}
    removePack = (id) => {this.selected_options = this.selected_options.filter(pack => pack !== id)}

    generateConfigOptions = (pack_data) => {
      console.log(pack_data)
      if(pack_data){
        return(
            pack_data.Packs.map((parent_pack) => {
                if(parent_pack.packs.length > 0){
                    return (
                    <div>
                        <Box sx={{ display: 'flex', flexDirection: 'column', ml: 1 }}>
                            <h3>{parent_pack.name}</h3>
                        </Box>
                    {parent_pack.packs.map((pack) => {
                                    if(pack.packs.length === 0){
                                        if(pack.isAutoEnabled === true){this.addPack(pack.id)}
                                        return (
                                            <Box sx={{ display: 'flex', flexDirection: 'column', ml: 5 }}>
                                                <FormControlLabel
                                                    label={
                                                        <Typography sx={{ fontFamily: 'Audiowide', color: 'white' }}>
                                                            {pack.name}
                                                        </Typography>
                                                    }
                                                    sx={{ fontFamily: 'Audiowide', color: 'white' }}
                                                    control={<Checkbox sx={{ fontFamily: 'Audiowide', color: 'white' }} onChange={(e) => this.handleConfigChange(e, pack.id)} defaultChecked={pack.isAutoEnabled}/>}
                                                />
                                            </Box>
                                        )
                                    }
                                    else {

                                        return (
                                            <div>
                                                <Box sx={{ display: 'flex', flexDirection: 'column', ml: 5 }}>
                                                <h4>-{pack.name}-</h4>
                                                </Box>
                                                {pack['packs'].map((child_pack) => {
                                                    if(child_pack.isAutoEnabled === true){this.addPack(child_pack.id)}
                                                    return (
                                                        <Box sx={{ display: 'flex', flexDirection: 'column', ml: 10 }}>
                                                            <FormControlLabel
                                                                label={
                                                                    <Typography sx={{ fontFamily: 'Audiowide', color: 'white' }}>
                                                                        {child_pack.name}
                                                                    </Typography>
                                                                }
                                                                theme={theme}
                                                                sx={{fontFamily: 'Audiowide', color: 'white'}}
                                                                control={<Checkbox sx={{ fontFamily: 'Audiowide', color: 'white' }} onChange={(e) => this.handleConfigChange(e, child_pack.id)} defaultChecked={child_pack.isAutoEnabled}/>}
                                                            />
                                                        </Box>
                                                    )
                                                })}
                                            </div>
                                        )
                                    }
                                }
                            )
                        }
                    </div>
                    )
                }
            })
        )
      }
      else {
        return (<div></div>)
      }
    }

    render() {
        return(
            <div className='tool-page'>
                <EndzoneNav links = {this.props.links}></EndzoneNav>
                <div className='tool-input-div'>
                    <EndzoneAlert ref={this.alertRef}></EndzoneAlert>
                    <div className='tool-header'>
                        <h3>{this.props.reportName}</h3>
                    </div>
                    <div className='tool-body'>
                        <div className='tool-games'>
                            <EndzoneGameSelect 
                                year_options = {this.state.year_options} 
                                team_options = {this.state.team_options} 
                                game_options = {this.state.game_options}
                                handleTeamChange = {(selectedList) => {this.setState({selected_teams: selectedList}, () => this.filterGames())}}
                                handleYearChange = {(selectedList) => {this.setState({selected_years: selectedList}, () => this.filterGames())}}
                                handleGameChange = {(selectedList) => {this.setState({selected_games: selectedList})}}
                                >
                            </EndzoneGameSelect>
                        </div>
                        <div className='tool-container'>
                            <EndzoneButton style={{"backgroundColor": this.state.isConfigureActivated ? '#3772a0' : "#353b48"}} onClick = {(e) => {this.activateConfigure()}} text = "Customize Report" icon = {<SettingsIcon sx = {{"font-size": '150%'}} ></SettingsIcon>}></EndzoneButton>
                        </div>
                        <div className='tool-container'>
                            <EndzoneButton onClick = {(e) => {this.runTool()}} style={{"backgroundColor": "#054f88"}} text = "Run Tool" icon = {<PlayCircleOutlineIcon sx = {{"font-size": '150%'}}></PlayCircleOutlineIcon>}></EndzoneButton>
                        </div>
                    </div>
                </div>
                <div className='tool-output-div'>
                    <div className='load-screen' id = "load-screen" style={{display: this.state.isBaseScreenActivated ? 'inline-flex' : 'none'}}>
                        <div className='load-div' id = "load-div" style={{animation: this.state.isLoading ? "loading_spin 1.25s linear infinite" : ''}}>
                            <img src={logo} className = "tool-logo"></img>
                            <h3 style={{opacity: this.state.isLoading ? 1 : 0}} className ='loading-text'>Loading...</h3>
                            <a href={this.state.documentUrl} style={{display: this.state.viewUrl ? 'block' : 'none'}}>Click to View Report</a>
                        </div>
                    </div>
                    <div className='configure-div' style={{display: this.state.isConfigureActivated ? 'inline-flex': 'none'}}>    
                        <div className='configure'>
                          {this.generateConfigOptions(this.state.pack_data)}
                        </div>    
                    </div>
                </div>
            </div>
        );
    }
}



export default ToolPage;