import React from 'react';
import "./endzone-game-select.css"
import EndzoneMultiSelect from '../endzone-multiselect/endzone-multiselect';
import EndzoneSelect from '../endzone-select/endzone-select';

class EndzoneGameSelect extends React.Component {
    constructor (props) {
        super(props);
    }
    handleMultiSelectChange = (selections) => {
        // Flattened list of select object
        console.log(selections)
        let selected_data_list = []
        selections.map((selection) => {
            selected_data_list.push(selection.value)
        })
        return selected_data_list
    }

    render() {
        return(
            <div className='game-selection-div'>
                <div className='game-selection-header'><h2>Select Games</h2></div>
                <div className='game-selection-body'>
                    <p>Filter Games by a Team of Interest (An Opponent or Yourself) or Year, then select a game below.</p>
                    <div className='select-search'>
                        <label className='game-select-labels'>Teams of Interest:</label>
                            <EndzoneSelect data = {this.props.team_options} handleChange = {(selection) => this.props.handleTeamChange(this.handleMultiSelectChange(selection))}>
                            </EndzoneSelect>
                        <label className='game-select-labels'>Search by Year:</label>
                            <EndzoneMultiSelect data = {this.props.year_options} handleChange = {(selection) => this.props.handleYearChange(this.handleMultiSelectChange(selection))}>
                            </EndzoneMultiSelect>
                    </div>
                    <div className='select-game-div'>
                        <label className='game-select-labels'>Select Games:</label>
                        <EndzoneMultiSelect data = {this.props.game_options} handleChange = {(selection) => this.props.handleGameChange(this.handleMultiSelectChange(selection))}>
                        </EndzoneMultiSelect>
                    </div>
                </div>
            </div>
        );
    }
}



export default EndzoneGameSelect;