import React from 'react';
import "./pre-game-report.css"

import ToolPage from '../../../components/tools/tool-page/tool-page';

class PreGameReport extends React.Component {
    constructor (props) {
        super(props);
    }
    render() {
        return(
            <div className='tool-page'>
                <ToolPage reportName = "Pre-Game Report" requestUrl = "/endzone/reports/pregame/run" links = {
                    [
                        {title: "Return to Pre-Game", link: "/endzone/pregame/hub"}
                    ]
                }>
                </ToolPage>
            </div>


        );
    }
}



export default PreGameReport;